import EvanCheckbox from '@/components/evan-checkbox/evan-checkbox.vue'
import EvanCheckboxGroup from '@/components/evan-checkbox/evan-checkbox-group.vue'
import UniIcons from '@/components/uni-icons/uni-icons.vue'
import {
	GetAddlist,
	RemoveAdd
} from "../../common/api.js"
export default {
	data() {
		return {
			list: [],
			pay_type:0
		}
	},
	components: {
		EvanCheckbox,
		EvanCheckboxGroup,
		UniIcons
	},
	onLoad(option) {
		this.pay_type = option.pay_type;
		this.pageInit();
	},
	methods: {
		addAddress() {
			uni.navigateTo({
				url: '../addAddress/addAddress?type=c'
			});
		},
		setDefault(param) {
			if (param.default) {
				param.default = true;
			}
		},
		pageInit() {
			GetAddlist().then(res => {
				if (res.status) {
					let d = [...res.data]
					for (let i = 0, len = d.length; i < len; i++) {
						if (d[i].default === 1) {
							d[i].default = true;
						} else {
							d[i].default = false;
						}
					}
					this.list = [...d];
				}
			})
		},
		toEdit(id) {
			uni.navigateTo({
				url: '../editAddress/editAddress?aid=' + id+'&type=c'
			});
		},
		fhSett(item){
			this.$store.commit("SET_DEFAADD",item);
			let t = this.pay_type;
			uni.navigateTo({
			    url: '../settlement/settlement?pay_type='+t
			});
		}
		

	}
}
